<template>
  <div class="login-container">
    <h2>로그인</h2>
    <div class="login-form">
      <input type="text" v-model="username" placeholder="사용자 이름" />
      <input type="password" v-model="password" placeholder="비밀번호" />
      <button @click="login">로그인</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LoginPage',  // 컴포넌트 이름을 LoginPage로 변경
  data() {
    return {
      username: '',
      password: ''
    };
  },
  methods: {
    login() {
      if (this.username === 'admin' && this.password === 'password') {
        this.$router.push('/user-management/user-list');
      } else {
        alert('잘못된 사용자 이름 또는 비밀번호입니다.');
      }
    }
  }
};
</script>

<style scoped>
.login-container {
  max-width: 400px;
  margin: 100px auto;
  padding: 2rem;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #fff;
  text-align: center;
}

.login-form input {
  display: block;
  width: 100%;
  padding: 0.5rem;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  padding: 0.5rem 1rem;
  background-color: #3f51b5;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #303f9f;
}
</style>
