<template>
  <div class="group-list">
    <h2>권한 관리</h2>

    <!-- 상단 네비게이션 -->
    <nav class="top-nav">
      <!-- 개인정보 클릭 시 이전 화면으로 돌아감 -->
      <a href="javascript:void(0)" @click="goBack">개인정보</a>
      <!-- 권한 관리로 이동 (현재 페이지) -->
      <router-link to="/user-management/group-list" class="active">권한 관리</router-link>
    </nav>

    <!-- 그룹 검색 및 권한 추가 버튼 -->
    <div class="action-container">
      <div class="search-container">
        <input type="text" v-model="searchQuery" placeholder="Search" />
        <button @click="searchGroup">검색</button>
      </div>
      <button class="add-group-button" @click="goToAddGroup">권한 추가</button>
    </div>

    <!-- 권한 리스트 테이블 -->
    <table>
      <thead>
        <tr>
          <th>권한 이름</th>
          <th>권한 설명</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="group in filteredGroups" :key="group.name">
          <td>{{ group.name }}</td>
          <td>{{ group.description }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchQuery: '',
      groups: [
        { name: '경영진', description: '회사의 주요 의사 결정을 내리는 사람들' },
        { name: '엔지니어', description: '기술 개발 및 유지보수 담당' },
        { name: '모델 관리자', description: '모델 관련 작업을 관리하는 사람들' },
      ]
    };
  },
  computed: {
    filteredGroups() {
      return this.groups.filter(group =>
        group.name.includes(this.searchQuery) || group.description.includes(this.searchQuery)
      );
    }
  },
  methods: {
    searchGroup() {
      console.log('검색어:', this.searchQuery);
    },
    goToAddGroup() {
      this.$router.push({ path: '/user-management/group-add' });
    },
    goBack() {
      this.$router.go(-1);  // 이전 페이지로 돌아가기
    }
  }
};
</script>

<style scoped>
.group-list {
  padding: 2rem;
  background-color: #ffffff;
  border: 1px solid #ddd;
  border-radius: 8px;
  max-width: 800px;
  margin: 2rem auto;
  font-size: 1.4rem;
}

.top-nav {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 1rem;
  border-bottom: 1px solid #ddd;
  padding-bottom: 0.5rem;
}

.top-nav a {
  margin-right: 1.5rem;
  text-decoration: none;
  color: #3f51b5;
  font-weight: bold;
  cursor: pointer;
}

.top-nav a.active {
  color: #303f9f;
  border-bottom: 2px solid #303f9f;
}

.action-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.search-container {
  display: flex;
  align-items: center;
}

input[type="text"] {
  padding: 0.5rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 0.5rem;
}

button {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  background-color: #3f51b5;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #303f9f;
}

.add-group-button {
  background-color: #4caf50;
}

.add-group-button:hover {
  background-color: #388e3c;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 1rem;
}

th, td {
  padding: 1rem;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

th {
  background-color: #f4f4f4;
}
</style>
