//수정해본거//
import { createRouter, createWebHistory } from 'vue-router';
import AdminLayout from '../layouts/AdminLayout.vue';
import UserList from '../components/UserList.vue';
import UserAdd from '../components/UserAdd.vue';
import UserDetail from '../components/UserDetail.vue';
import GroupList from '../components/GroupList.vue';  // 권한 관리 화면
import GroupAdd from '../components/GroupAdd.vue';  // 권한 추가 화면
import LoginPage from '../components/LoginPage.vue';  // LoginPage로 변경

const routes = [
  {
    path: '/user-management',
    component: LoginPage,  // LoginPage 컴포넌트를 사용
  },
  {
    path: '/',
    redirect: '/user-management/user-list'
  },
  {
    path: '/user-management/user-list',
    component: AdminLayout,
    children: [
      {
        path: '',
        name: 'UserList',
        component: UserList
      }
    ]
  },
  {
     path: '/user-management/user-add',
  component: AdminLayout,
  children: [
    {
      path: '',
      name: 'UserAdd',
      component: UserAdd
      }
    ]
  },
  {
    path: '/user-management/user-detail/:userId',
    component: AdminLayout,
    children: [
      {
        path: '',
        name: 'UserDetail',
        component: UserDetail
      }
    ]
  },
  {
    path: '/user-management/group-list',  // 권한 관리 경로
    component: AdminLayout,
    children: [
      {
        path: '',
        name: 'GroupList',
        component: GroupList
      }
    ]
  },
  {
  path: '/user-management/group-add',
  component: AdminLayout,
  children: [
    {
      path: '',
      name: 'GroupAdd',
      component: GroupAdd
    }
  ]
}
];

const router = createRouter({
  history: createWebHistory(),
  routes
});

export default router;

