<template>
  <div class="user-detail">
    <div v-if="user">
      <p><strong>이름:</strong> {{ user.name }}</p>
      <p><strong>사번:</strong> {{ user.employeeNo }}</p>

      <!-- 권한 체크박스 -->
      <p><strong>권한:</strong></p>
      <label><input type="checkbox" v-model="userRoles" value="경영진" /> 경영진</label>
      <label><input type="checkbox" v-model="userRoles" value="엔지니어" /> 엔지니어</label>
      <label><input type="checkbox" v-model="userRoles" value="모델관리자" /> 모델 관리자</label>

      <p><strong>최근 기록:</strong> {{ user.lastLogin }}</p>
    </div>
    <div v-else>
      <p>사용자 정보를 가져오는 중입니다...</p>
    </div>

    <button @click="goBack">뒤로가기</button>
    <button @click="saveUser">저장</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: null,
      userRoles: []  // 체크된 권한을 저장할 배열
    };
  },
  created() {
    const userId = this.$route.params.userId;
    this.fetchUser(userId);
  },
  methods: {
    fetchUser(userId) {
      const users = [
        { id: 1, name: '한수현', employeeNo: 1001, position: '경영진', lastLogin: '2024-10-12', roles: ['경영진'] },
        { id: 2, name: '서희림', employeeNo: 1002, position: '엔지니어', lastLogin: '2024-10-13', roles: ['엔지니어'] },
        { id: 3, name: '이규섭', employeeNo: 1003, position: '엔지니어', lastLogin: '2024-10-14', roles: ['엔지니어'] },
        { id: 4, name: '김세진', employeeNo: 1004, position: '모델관리자', lastLogin: '2024-10-15', roles: ['모델관리자'] }
      ];
      this.user = users.find(user => user.id === parseInt(userId));
      this.userRoles = this.user.roles;  // 기존 권한을 체크박스에 반영
    },
    goBack() {
      this.$router.push('/user-management/user-list');
    },
    saveUser() {
      // 사용자 권한을 저장하는 로직
      console.log("저장된 사용자 권한:", this.userRoles);
      alert("사용자 권한이 저장되었습니다!");
    }
  }
};
</script>

<style scoped>
.user-detail {
  font-size: 1.4rem;
  padding: 2rem;
  background-color: #ffffff;
  border: 1px solid #ddd;
  border-radius: 8px;
  max-width: 600px;
  margin: 2rem auto;
}

button {
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  background-color: #3f51b5;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #303f9f;
}
</style>
