<template>
  <div>
    <h1>사용자 추가</h1>
    <input type="text" v-model="name" placeholder="이름">
    <input type="number" v-model.number="employeeNo" placeholder="사번">
    <input type="text" v-model="role" placeholder="권한">
    <button @click="addUser">사용자 추가</button>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      name: "",
      employeeNo: "",
      role: ""
    };
  },
  methods: {
    async addUser() {
      try {
        const user = {
          id: Date.now(), // 임시 고유 ID
          name: this.name,
          employeeNo: this.employeeNo,
          role: this.role,
          last_login: null
        };
        await axios.post("http://ec2-18-215-52-54.compute-1.amazonaws.com:8000/user-management/user-add", user);
        this.$router.push({ path: '/user-management/user-add' });
      } catch (error) {
        console.error("사용자 추가에 실패했습니다", error);
      }
    }
  }
};
</script>